@import url(https://fonts.googleapis.com/css2?family=Gruppo&family=Barlow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gruppo&family=Barlow&family=Raleway:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gruppo&family=Barlow&display=swap);
@import url(https://fonts.googleapis.com/css?family=Chivo:300,300i,400,400i,700,700i,900,900i|Saira+Extra+Condensed:100,200,300,400,500,600,700,800|Saira:100,200,300,400,500,600,700,800);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}a{text-decoration:none}

.navbar{width:100%;height:80px;background-color:#101936;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.navbar a{color:white}.navbar a:hover{color:#6ace9c;padding-bottom:5px;border-bottom:1px solid #6ace9c}.navbar .logo{margin-left:10%;font-family:'Gruppo';font-size:3rem;color:white}.navbar .navbar-links{display:-webkit-flex;display:flex;list-style:none;margin-right:25%}.navbar .navbar-links .nav-link{font-family:'Barlow';font-size:1.2rem;padding:0 0 0 5rem}.navbar .navbar-links .contact{color:#6ace9c}

*{background-color:#101936}.intro{display:-webkit-flex;display:flex;width:100%;height:calc( 100vh - 80px);background-color:#101936;color:#ffffff}.intro .social-container{min-width:70px;height:350px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;box-shadow:2px 5px 20px #000;position:absolute;top:37%}.intro .social{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.intro .icon{cursor:pointer;font-size:1.7rem;margin:15px 0 15px 0}.intro .icon:hover{color:#6ace9c}.intro .intro-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;margin-left:10%;margin-right:10%;width:600px;font-family:'Barlow'}.intro .hello{font-size:2rem}.intro .name{font-family:'Raleway';font-size:3.5rem;margin-top:10px;white-space:nowrap}.intro .title{font-size:1.6rem;margin-top:15px}.intro .bio{font-size:1rem;margin-top:50px}.intro .picture-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;min-width:800px;margin-right:5%}.intro .picture-container .profile-pic{width:100%;opacity:0.5}

a{color:white}a:hover{color:#6ace9c}body{color:white}.about-container{width:100%;height:80vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.about-container .left-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:40%;margin-left:auto;margin-right:auto}.about-container .left-container .title{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-bottom:20px}.about-container .left-container .title h1{font-size:1rem;color:#6ace9c}.about-container .left-container .title .title-bar{width:20%;height:1px;background-color:#6ace9c;margin:3px 0 0 3px}.about-container .left-container .quote{font-size:1rem;font-family:Trebuchet MS;margin-bottom:50px}.about-container .right-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:40%}.about-container .right-container .query{font-size:2rem;font-style:italic}.about-container .right-container .query .arrow{margin-left:50%}

.portfolio-container{width:100%;min-height:100vh}.portfolio-container .portfolio-grid{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin-top:10rem}.portfolio-container .portfolio-grid .grid-item{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin:10px}.portfolio-container .portfolio-grid .grid-item button{width:100px;height:30px;background-color:#6ace9c;border:none;border-radius:0px;margin:5px}

body{font-size:16px}body p{font-family:'Chivo';font-size:14px;margin:20px 0 0 20px}body strong{font-weight:600}body .timeline-content{text-align:center}body .timeline-content .title-container{width:100%;height:calc(100vh - 180px);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}body .timeline-content .arrow{font-size:2.4rem;padding-bottom:10rem}body .timeline-content .timeline{border-left:4px solid #6ace9c;border-bottom-right-radius:4px;border-top-right-radius:4px;background:fade(#fff, 3%);color:fade(white, 80%);font-family:"Chivo",sans-serif;margin:50px auto;letter-spacing:0.5px;position:relative;line-height:1.4em;font-size:1.03em;padding:50px;list-style:none;text-align:left;font-weight:100;max-width:40%}body .timeline-content h1{font-family:"Saira",sans-serif;letter-spacing:1.5px;font-weight:100;font-size:5.3rem}body .timeline-content h2,body .timeline-content h3{font-family:"Saira",sans-serif;letter-spacing:1.5px;font-weight:400;font-size:1.6em}body .timeline-content .quote{margin-top:5rem}body .event{border-bottom:1px dashed fade(#fff, 10%);padding-bottom:25px;margin-bottom:50px;position:relative}body .event:last-of-type{padding-bottom:0;margin-bottom:0;border:none}body .event:before,body .event:after{position:absolute;display:block;top:0}body .event:before{left:-217.5px;color:fade(#fff, 40%);content:attr(data-date);text-align:right;font-weight:100;font-size:0.9em;min-width:120px;font-family:"Saira",sans-serif}body .event:after{box-shadow:0 0 0 4px fade(#6ace9c, 100%);left:-60.05px;background:#101936;border:3px solid #6ace9c;border-radius:50%;height:11px;width:11px;content:"";top:5px}

.contact{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;position:relative}.contact .contact-container{background-color:#fff;width:1100px;height:600px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;box-shadow:20px 30px 9px #040a1d;margin-top:7rem}.contact .contact-form{background:-webkit-gradient(linear, left top, left bottom, color-stop(0%, #5cffad), color-stop(100%, #06753e));color:#000;width:45%;height:100%}.contact .contact-form h1{color:#101936;background-color:transparent;font-family:'Lato'}.contact .contact-form form{background-color:transparent;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-top:5rem}.contact .contact-form form input,.contact .contact-form form textarea,.contact .contact-form form button{background-color:transparent;width:90%;margin:15px}.contact .contact-form form input{background-color:transparent;height:30px;border:none;border-bottom:1px solid #000}.contact .contact-form form textarea{height:150px;border:none;border-bottom:1px solid #000}.contact .contact-form form button{background-color:transparent;color:#000000;margin-top:20px;width:100px;height:30px;border:none;border-radius:30px;cursor:pointer;box-shadow:1px 1px 2px #000000}.contact .contact-form form button:hover{background-color:transparent;border:none;box-shadow:1px 1px 5px #000000 inset}.contact .map{width:55%;height:100%}.contact .map img{min-width:100%;min-height:100%;width:auto;height:auto}

